.container {
	padding-left: 13px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: var(--dx-header-height);
	background: var(--dx-app-bg);
}
.accountLabel {
	margin-right: 60px;
	padding-left: 10px;
	display: flex;
	align-items: center;
	height: 31px;
	font-size: 14px;
	text-transform: capitalize;
	color: var(--dx-header-badge-demo);
	border-left: solid 1px var(--dx-header-badge-demo);
}

.accountLabel_real {
	color: var(--dx-header-badge-real);
	border-left: solid 1px var(--dx-header-badge-real);
}
