.container {
	z-index: 11;
	position: absolute;
	top: -2px;
	left: 0;
	box-sizing: border-box;
}

	.container_hasArrow {
		composes: container;
	}

	.container_hasArrow.container_placementTop {
			 padding-bottom: 8px;
		}

	.container_hasArrow.container_placementTop .arrow {
				top: 100%;
				border-width: 8px 8px 0 8px;
				border-color: #ffffff transparent transparent transparent;
			}

	.container_hasArrow.container_placementBottom {
			 padding-top: 8px;
		}

	.container_hasArrow.container_placementBottom .arrow {
				bottom: 100%;
				border-width: 0 8px 8px 8px;
				border-color: transparent transparent #ffffff transparent;
			}

	.container_hasArrow.container_placementLeft {
			 padding-right: 8px;
		}

	.container_hasArrow.container_placementLeft .arrow {
				left: 100%;
				border-width: 8px 0 8px 8px;
				border-color: transparent transparent transparent #ffffff;
			}

	.container_hasArrow.container_placementRight {
			 padding-left: 8px;
		}

	.container_hasArrow.container_placementRight .arrow {
				right: 100%;
				border-width:8px 8px 8px 0;
				border-color: transparent #ffffff transparent transparent;
			}

.content {
	position: relative;
	padding: 5px;

	background-color: var(--dx-dropdown-list-bg);
	border: 2px solid var(--dx-dropdown-list-border);
	border-radius: var(--dx-standart-border-radius);
	color: var(--dx-dropdown-list-default-text);

	animation-name: slide-in-from-top, fade-in;
	animation-duration: .2s;
	animation-timing-function: ease;
}

.content:not(:empty) {
		box-shadow: 0 0 20px 0 var(--dx-dropdown-list-shadow);
	}

.arrow {
	display: block;
	width: 0;
	height: 0;
	position: absolute;
	border-style: solid;
}
