.container {
	border-radius: 4px;
	min-height: 30px;
	font-size: 14px;
	padding: 5px 10px;
	width: 100px;
	border: 1px solid var(--dx-button-secondary-default-border);
	position: relative;
	background: var(--dx-button-secondary-default-bg);
	color: var(--dx-button-secondary-default-text);
	outline: none;
}

	.container:hover {
		border-color: var(--dx-button-secondary-default-border);
		background: var(--dx-button-secondary-hover-bg);
	}

	.container:active {
		border-color: var(--dx-button-secondary-default-border);
		background: var(--dx-button-secondary-pressed-bg);
	}

	.container:disabled {
		border-color: var(--dx-button-secondary-disabled-border);
		background: var(--dx-button-secondary-disabled-bg);
		color: var(--dx-button-secondary-disabled-text);
	}

.inner {
	position: relative;
}

.inner_isLoading {
		visibility: hidden;
	}

.container_primary {
	background: var(--dx-button-primary-default-bg);
	border-color: var(--dx-button-primary-default-border);
	color: var(--dx-button-primary-default-text);
	font-size: 21px;
	font-weight: 600;
}

.container_primary:hover {
		background-color: var(--dx-button-primary-hover-bg);
		border-color: var(--dx-button-primary-default-border);
	}

.container_primary:active {
		background-color: var(--dx-button-primary-pressed-bg);
		border-color: var(--dx-button-primary-default-border);
	}

.container_primary:disabled {
		background-color: var(--dx-button-primary-disabled-bg);
		border-color: var(--dx-button-primary-disabled-border);
		color: var(--dx-button-primary-disabled-text);
	}
