.error {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	height: 100%;
	width: 100%;
	padding: 30px 20px 10px;

	text-align: left;

	background-color: var(--dx-form-global-error-bg);
	color: var(--dx-form-global-error-text);
}

	.error::before {
		display: block;
		position: absolute;
		top: -20px;
		right: 0;
		left: 0;
		height: 20px;

		content: '';
		-webkit-backdrop-filter: blur(50px);
		backdrop-filter: blur(50px);
		background-image: linear-gradient(to top, #0c2234, rgba(31, 37, 51, 0.1));
	}

	.error__icon {
		width: 40px;
		height: 40px;
		margin-right: 15px;
		color: var(--dx-form-global-error-icon);
	}

	.error__title {
		margin-bottom: 3px;
		font-size: 20px;
	}

	.error__message {
		max-width: 260px;
		font-size: 14px;
		color: var(--dx-form-global-error-message-text);
	}
