.container {
	width: 262px;
	height: 50px;
	border-radius: 80px;
	font-size: 16px;
	position: fixed;
	right: 40px;
	bottom: 40px;
	background: var(--dx-button-continue-registration-bg);
	display: flex;
	align-items: center;
	padding-left: 17px;
	color: var(--dx-button-continue-registration-font-color);
}

.title {
	font-size: 16px;
	font-weight: bold;
	padding: 0;
	margin-right: 11px;
}

.btn {
	background: none;
	width: 20px;
	height: 20px;
	padding: 0;
	border: none;
	color: var(--dx-button-continue-registration-font-color);
	margin-left: 5px;
	outline: none;
}
