.container {
	box-sizing: border-box;

	min-width: 111px;
	position: relative;
	overflow: hidden;
	display: inline-block;
	font-size: 10px;
	line-height: 12px;
	font-weight: normal;
	background-color: var(--dx-chart-infobox-bg);
	border-radius: 10px;
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}

.containerMobile {
	padding: 5px 10px;
}

.containerMobile .header {
		height: 29px;
		padding-bottom: 5px;
		padding-top: 5px;
	}

.containerMobile .toggle {
		display: none;
	}

.containerMobile .label {
		font-size: 16px;
  		font-weight: normal;
  		line-height: 1.38;
	}

.containerMobile .title {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.36;
		color: var(--dx-chart-infobox-header-text);
	}

.containerDesktop {
	padding: 5px 25px 0 25px;
}

.header {
	width: 100%;
	height: 23px;
	line-height: 23px;
	white-space: nowrap;
	padding-right: 24px;
	box-sizing: border-box;
}
.title {
	padding-left: 8px;
	font-size: 12px;
	box-sizing: border-box;
	height: 100%;
	display: block;

	color: var(--dx-chart-infobox-header-text);
}
.toggle {
	position: absolute;
	top: 5px;
	left: 5px;
	display: inline-block;
	vertical-align: top;
	border: none;
	height: 24px;
	width: 24px;
	text-align: center;
	padding: 0;
	outline: none;

	background-color: transparent;
	color: var(--dx-chart-infobox-icon);
	transition: background-color 100ms ease-in, transform 100ms ease-in;
}
.toggle:hover {
		background-color: var(--dx-chart-infobox-button-hover);
	}
.toggle:active {
		background-color: var(--dx-chart-infobox-button-pressed);
	}
.toggle .icon {
		width: 9px;
		height: 9px;
	}
.content {
	overflow: hidden;
	height: calc(100% - 23px);
}
.group {

	box-sizing: border-box;
	display: table;
	min-width: 100%;
	padding-left: 8px;
	padding-right: 0;
	margin-bottom: 10px;
}
.group:first-child {
		padding-top: 2px;
	}
.group_volume {
		margin-bottom: 4px;
	}
.group_volume + .studies:not(:empty) {
				padding-top: 6px;
				border-top-width: 1px;
				border-top-style: solid;
			}
.group_volume .value {
			color: var(--dx-chart-infobox-value-text);
		}
.group_candle .value {
			color: var(--dx-chart-infobox-value-text);
		}
.group_up .value {
			color: var(--dx-chart-infobox-value-up-text);
		}
.group_down .value {
			color: var(--dx-chart-infobox-value-down-text);
		}
.groupTitle {
	padding-left: 8px;
	padding-right: 8px;
	box-sizing: border-box;
	display: table-caption;
	padding-bottom: 3px;

	color: var(--dx-chart-infobox-header-text);
}

.line {
	opacity: 0.8;
	border-color: var(--dx-chart-cursor-line);
}

.parameter {
	box-sizing: border-box;
	display: table-row;
}

.label {
	box-sizing: border-box;
	display: table-cell;
	padding-right: 25px;
	min-width: 30px;
	font-size: 14px;
	line-height: 22px;
	color: var(--dx-chart-infobox-value-text);
}

.label:not(:last-child) {
		padding-bottom: 1px;
	}

.value {
	display: table-cell;
	font-size: 16px;
	text-align: right;

	color: white; /*/ $chart-cross-label-text-color; */
}

.delimeter {
	height: 1px;
	margin-bottom: 6px;
}

.line_vertical,
	.line_horizontal {
		border-style: solid;
	}

.overlay {
	/*/this is main ChartInfo (overlaying) */
	margin-top: 5px;
	margin-left: 10px;
	max-height: calc(100% - 40px);
}

.collapsed {
	width: auto;
	min-width: 0;
	height: auto;
	padding: 0;
	margin-left: 15px;
	margin-top: 10px;
	border-radius: 0;
}

.collapsed .header {
		position: relative;
		padding-right: 0;
	}

.collapsed .title {
		display: none;
	}

.collapsed .content {
		display: none;
	}

.collapsed .toggle {
		position: relative;
		transform: rotate(180deg);
		left: 0;
		top: 0;
	}
