.backdrop {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	text-align: center;
	background: rgba(0,0,0,.7);

	/* To keep above chart tools */
	z-index: 5;
}

	.backdrop:before {
		 content: '';
		 display: inline-block;
		 vertical-align: middle;
		 width: 0;
		 height: 100%;
	}

	.backdrop:not(.backdrop_isModal) {
		pointer-events: none;
	}

	.backdrop:not(.backdrop_isModal) .container {
			pointer-events: auto;
		}

.container {
	padding: 0;
	vertical-align: middle;
	display: inline-block;
	text-align: left;
	border-radius: 10px;
	font-family: var(--dx-font-base);
	border: 0 solid var(--dx-toolbar-border);
	background-color: var(--dx-toolbar-bg);
}

.header {
	padding: 0 10px;
	position: relative;
	display: flex;
	height: 49px;
	align-items: center;
	justify-content: center;
}

.title {
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
}

.body {
	overflow: hidden;
}
