.container {
	display: flex;
	width: 100%;
	height: 100%;
}

.left {
	display: flex;
	flex: 1;
	height: 100%;
	position: relative;
	align-items: stretch;
	flex-direction: column;
	margin-right: var(--dx-side-panel-spacing);
}

.aside {
	display: flex;
	position: relative;
	width: var(--dx-side-panel-width);
	height: 100%;
	overflow: hidden;

	border-radius: var(--dx-standart-border-radius) 0 0 0;
	background-color: var(--dx-widget-bg);
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}
