.content {
	padding-top: 20px;
}

.controlsWrapper {
	display: flex;
	flex-wrap: wrap;
	margin-top: 0;
	position: relative;
	padding-bottom: 10px;
}

.profitControlsWrapper {
	margin-left: 2px;
}

.element {
	width: 50%;
}

.element:nth-child(n+3) {
		margin-top: 22px;
	}

.element:nth-child(2n+2) {
		padding-left: 35px;
	}

.priceLabel,
.priceValue,
.label {
	color: var(--dx-form-label-text);
	font-size: 14px;
	margin-bottom: 7px;
}

.limitLabel {
	margin-top: 2px;
}

.checkboxWrapper {
	margin-top: 1px;
	margin-bottom: 10px;
}

.priceWrapper {
	display: flex;
}

.priceValue {
	margin-left: auto;
	margin-right: 25px;
}

.plLabel {
	font-size: 16px;
	margin-top: 12px;
}

.controls {
	width: 50%;
}

.error {
	border-radius: 3px;
	font-size: 14px;
	display: flex;
	color: var(--dx-takeprofit-errror-text);
	justify-content: flex-start;
	align-items: center;
	bottom: 1px;
	border: 1px solid var(--dx-form-input-error-border);
}

.errorIcon {
	margin-right: 10px;
	width: 40px;
	height: 40px;
}
