.container {
	display: flex;
	width: 100%;
	height: 100%;
	/*flex-direction: column;*/
	align-items: stretch;
}

.left {
	display: flex;
	flex: 1;
	height: 100%;
	position: relative;
	align-items: stretch;
	flex-direction: column;
	margin-right: var(--dx-side-panel-spacing);
}

.chart {
	border-radius: var(--dx-standart-border-radius);
	background-color: var(--dx-widget-bg);
	height: 300px;
	margin-bottom: 8px;
	position: relative;
	overflow: hidden;
}

.aside {
	border-radius: var(--dx-standart-border-radius) 0 0 0;
	background-color: var(--dx-widget-bg);
	height: 100%;
	display: flex;
	position: relative;
	width: var(--dx-side-panel-width);
	overflow: hidden;
}
