.symbol {
	margin-left: 1px;
	margin-top: 2px;
	font-size: 20px;
	font-weight: 600;
	color: var(--dx-form-text);
}

.description {
	margin-left: 2px;
	font-size: 16px;
	color: var(--dx-form-label-text);
	position: relative;
	top: -3px;
}

.info {
	display: flex;
	flex-wrap: wrap;
	margin-top: 23px;
}

.element {
	width: 50%;
}

.element:nth-child(n+3) {
		margin-top: 17px;
	}

.element:nth-child(2n+2) {
		padding-left: 35px;
	}

.profitWrapper {
	margin: 20px 0 0;
}

.profitLabel {
	display: inline-block;
	font-size: 16px;
	padding-right: 20px;
	color: var(--dx-form-label-text);
}

.profitAmount {
	font-size: 18px;
	font-weight: 600;
}

.profitAmount_positive {
	color: var(--dx-form-up-text);
}

.profitAmount_positive:before {
		content: '+';
	}

.profitAmount_negative {
	color: var(--dx-form-down-text);
}

.element_label {
	color: var(--dx-form-label-text);
	font-size: 14px;
}

.element_text {
	color: var(--dx-form-text);
	font-size: 16px;
	margin-top: 5px;
}

.divider {
	width: 100%;
	height: 1px;
	background-color: var(--dx-table-divider-line);
}

.verticalSpacer {
	height: 216px;
}

.closeBtnWrapper {
	margin-left: auto;
}

.takeProfitStopLossWrapper {
	padding-right: 20px;
}
