.container {
	width: auto;
	margin: 0 auto;
	padding: 5px 32px 4px;

	font-size: 16px;
	font-weight: 600;
	line-height: 22px;

	text-transform: uppercase;
	border-radius: 18px;
	background: var(--dx-buy-button-secondary-default-bg);
	color: var(--dx-buy-button-secondary-default-text);
	border: 2px solid var(--dx-buy-button-secondary-default-border);
	cursor: default;
}

	.container:hover {
		background-color: var(--dx-buy-button-secondary-hover-bg);
		border-color: var(--dx-buy-button-secondary-hover-border);
		color: var(--dx-buy-button-secondary-hover-text);
	}

	.container:active {
		background-color: var(--dx-buy-button-secondary-pressed-bg);
		border-color: var(--dx-buy-button-secondary-pressed-border);
		color: var(--dx-buy-button-secondary-pressed-text);
	}

	.container:disabled {
		background-color: var(--dx-buy-button-secondary-disabled-bg);
		border-color: var(--dx-buy-button-secondary-disabled-border);
		color: var(--dx-buy-button-secondary-disabled-text);
	}
