.content {
	min-height: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.innerPaddedTopmostWrapper, .innerPaddedWrapper {
	padding-left: 20px;
	padding-right: 20px;
}

.innerPaddedWrapper {
	border-top: solid 1px var(--dx-form-divider-line);
}

.divider {
	width: 100%;
	height: 1px;
	background-color: var(--dx-form-divider-line);
}

.operations {
	display: flex;
	justify-content: space-around;
	padding: 0 20px;
	cursor: default;
}

.info {
	display: flex;
	flex-wrap: wrap;
}

.element {
	width: 50%;
	margin: 20px 0 10px;
}

.element:nth-child(2n+2) {
		padding-left: 35px;
	}

.label {
	color: var(--dx-form-label-text);
	font-size: 14px;
	margin-bottom: 8px;
}

.title {
	color: var(--dx-table-header-text);
	font-size: 20px;
	line-height: 27px;
}
.text {
	color: var(--dx-form-text);
	font-size: 16px;
	margin-top: 11px;
}

.moreOptions {
	position: relative;
	width: 160px;
	margin: 21px 0 20px;
	padding-left: 16px;
	color: var(--dx-link-text);
	font-size: 16px;
	cursor: default;
}

.moreOptions:before {
		position: absolute;
		top: 5px;
		left: 1px;
		content: '';
		display: block;
		width: 0;
		height: 0;
		border: 6px solid transparent;
		border-left: 6px solid var(--dx-link-text);
		border-radius: 2px;
	}

.moreOptions:hover {
		color: var(--dx-link-hover-text);
	}

.moreOptions:hover:before {
			border-left-color: var(--dx-link-hover-text);
		}

.moreOptions_opened:before {
		top: 7px;
		left: -3px;
		transform: rotate(90deg);
	}

.optionsItem {
	padding: 20px 0;
}

.priceHits {
	margin-top: 20px;
}

.cancelOnDateCheckbox {
	margin: 52px 10px 0 33px;
}

.cancelOnDate {
	margin-top: 20px;
}

.validationError {
	border-radius: 3px;
	background-color: var(--dx-form-input-error-bg);
	font-size: 12px;
	color: var(--dx-form-input-error-text);
	position: absolute;
	padding: 0 10px;
	display: flex;
	align-items: center;
	border: 1px solid var(--dx-form-input-error-border);
}
