.container {
	display: flex;
	width: 100%;
	height: 100%;
	padding-bottom: 8px;
}

.left {
	display: flex;
	flex: 1;
	height: 100%;
	position: relative;
	align-items: stretch;
	flex-direction: column;
	margin-right: var(--dx-side-panel-spacing);
}

.filterFormWrapper {
	height: 52px;
	margin-bottom: 8px;
}

.historyTableWrapper {
	width: 100%;
	height: 100%;
	border-radius: var(--dx-standart-border-radius);
	overflow: hidden;
}

.aside {
	display: flex;
	width: var(--dx-side-panel-width);
	height: 100%;
	position: relative;
	overflow: hidden;
	border-radius: var(--dx-standart-border-radius) 0 0 0;
	background-color: var(--dx-widget-bg);
}

.emptyScreen {
	width: 100%;
	margin-right: 8px;
	padding-bottom: 8px;
}
