.container {
	margin: 8px auto 0;
	padding: 0;
	display: block;
	font-family: var(--dx-font-base);
	font-size: 14px;
	min-height: auto;
	text-decoration: underline;
	color: var(--dx-infobox-close-link);
	background: none;
	border: none;
	outline: none;
}
	.container:hover {
		color: var(--dx-link-hover-text);
		border: none;
		outline: none;
		background: none;
	}
