.container {
	position: relative;
	width: 24px;
	height: 24px;
	min-height: 24px;
	border: 0;
	padding: 0;
	background: transparent;
	display: flex;
}

.container:hover {
	background: var(--dx-calendar-button-bg-hover);
}

.container:active {
	background: var(--dx-calendar-button-bg-pressed);
}

.container .icon {
	height: 15px;
}
