.container {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	font-size: 16px;
	padding: 9px 20px;
	cursor: default;
}

	.container_selected {
		font-weight: bold;
	}

.id {
	color: var(--dx-menu-account-id);
	margin-right: 10px;
}

.icon {
	width: 20px;
	height: 20px;
	display: block;
}

.currency {
	color: var(--dx-menu-account-currency);
	margin-right: 13px;
}

.status {
	color: var(--dx-menu-account-currency);
	text-transform: uppercase;
	margin-right: 10px;
}

.status_demo {
		color: var(--dx-menu-badge-demo);
	}
