.container {
	flex-shrink: 0;
	width: 31px;
	height: 31px;
	background-color: var(--dx-stepper-default-bg);
	color: var(--dx-stepper-default-icon);
	padding: 9px;
}

	.container:hover {
		background-color: var(--dx-stepper-hover-bg);
		color: var(--dx-stepper-hover-icon);
	}

	.container:active {
		background-color: var(--dx-stepper-pressed-bg);
		color: var(--dx-stepper-pressed-icon);
	}

	.container:disabled,
	.container_isDisabled {
		background-color: var(--dx-stepper-disabled-bg);
		color: var(--dx-stepper-disabled-icon);
	}
