.scrollable {
	overflow: hidden;
	position: relative;
}

.withVerticalScrollbar:not(.overlayContent) > .wrapper > .container > .content {
		padding-right: 0;
	}

.vertical_scrollbar__bar {
	min-height: 10px;
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
}

.wrapper {
	display: block;
	width: 100%;
	height: 100%;
	max-height: inherit;
	background-color: inherit;
}

.container {
	padding: 0;
	overflow: scroll;
	max-height: inherit;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
}

.container::-webkit-scrollbar {
		display: none;
	}

.content {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	min-width: 100%;
	box-sizing: border-box;
}

.resizeDetector {
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	border: none;
}

.scrollbar {
	background-color: transparent;
}
