.container {
	background: var(--dx-toolbar-bg);
	display: flex;
	flex-direction: column;
}
.header {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: space-between;
}
.headerLabel {
	color: var(--dx-table-header-text);
	font-size: 20px;
	line-height: 27px;
}
.content {
	padding: 0 15px 0 17px;
	height: 100%;
}
.profit {
	margin-bottom: 20px;
}
.profitLabel {
	display: inline-block;
	font-size: 16px;
	padding-right: 20px;
	color: var(--dx-form-label-text);
}
.profitValue {
	font-size: 18px;
	font-weight: 600;
}
.profitValue_positive {
	color: var(--dx-form-up-text);
}
.profitValue_positive:before {
		content: '+';
	}
.profitValue_negative {
	color: var(--dx-form-down-text)
}
.columns {
	display: flex;
}
.amount{
	width: 200px;
	color: var(--dx-form-label-text)
}
.amountHeader {
	display: flex;
	justify-content: space-between;
	margin-bottom: 7px;
}
.lot {
	display: flex;
	padding-left: 60px;
	flex-direction: column;
}
.lotLabel {
	margin-bottom: 7px;
	font-size: 14px;
	color: var(--dx-form-label-text);
}
.lotValue {
	margin-top: 4px;
	color: var(--dx-form-text);
	font-size: 16px;
	word-break: break-all;
	word-wrap: break-word;
}
