.container {
	margin-bottom: 8px;
	position: relative;
	display: flex;
	flex: 1;
	border-radius: var(--dx-standart-border-radius);
	overflow: hidden;
	background-color: var(--dx-widget-bg);
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}

.bottom {
	margin-bottom: 8px;
	padding: 0 20px 10px;
	position: relative;
	height: 300px;
	overflow: hidden;
	border-radius: var(--dx-standart-border-radius);
	background-color: var(--dx-widget-bg);
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}

.panel {
	flex: 0;
	margin-bottom: 8px;

	border-radius: var(--dx-standart-border-radius);
	background-color: var(--dx-widget-bg);
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}
