.container {
	padding: 0;
	width: auto;
	min-height: 0;
	color: var(--dx-table-icon);
	background: none;
	border: none;
	cursor: default;
}

.container:hover, .container:active {
	border: none;
}

.container:hover {
	background: var(--dx-close-button-hover-bg);
	border-radius: 4px;
	color: var(--dx-table-hover-icon);
}

.container:active {
	color: var(--dx-table-pressed-icon);
}
