.scrollbar {
	border-radius: 1px;
	background: var(--dx-scrollbar-default-bg);
}
	.scrollbar:hover, .scrollbar:active {
		background: var(--dx-scrollbar-active-bg);
	}

.container_hasShadow {
		box-shadow: inset 0 -50px 25px -25px var(--dx-widget-inner-shadow);
	}

.track {
	right: 4px;
	top: 3px;
	bottom: 3px;
	width: 3px !important;
}
