@import '../../lines/theme/line.component.module.css';

.container {
	position: absolute;
	left: 0;
	width: 100%;
	border-bottom: 1px solid var(--dx-chart-current-price-line);
}

.price {
	background-color: var(--dx-chart-current-price-up-bg);
	color: var(--dx-chart-current-price-text);
}

.price_down {
	background-color: var(--dx-chart-current-price-down-bg);
	color: var(--dx-chart-current-price-text);
}
