.container {
	flex-shrink: 0;
	position: relative;
    top: -2px;
    right: -5px;
	width: 30px;
	height: 30px;
	padding: 0;
	box-sizing: border-box;

	text-align: center;

	border: none;
	background: var(--dx-date-input-button-default-bg);
	color: var(--dx-date-input-button-default-icon);
}

	.container:hover {
		background: var(--dx-date-input-button-hover-bg);
		color: var(--dx-date-input-button-hover-icon);
	}

	.container:active {
		background: var(--dx-date-input-button-pressed-bg);
		color: var(--dx-date-input-button-pressed-icon);
	}

	.container:disabled {
		background: var(--dx-date-input-button-disabled-bg);
		color: var(--dx-date-input-button-disabled-icon);
	}
