.container {
	display: flex;
	flex-shrink: 0;
	padding: 10px 15px 8px 10px;

	background-color: var(--dx-toolbar-bg);
}

.groupTitle {
	display: flex;
	align-items: center;
	height: 10px;
	margin-bottom: 13px;
	margin-top: 15px;
	padding-left: 10px;
	padding-right: 15px;
}

.groupTitle:first-child {
		margin-top: 9px;
	}

.groupTitle:after {
		flex-grow: 1;
		border-bottom: 1px solid var(--dx-symbol-search-line);
		content: '';
	}

.groupTitleText {
	flex-shrink: 0;
	margin-right: 6px;

	font-size: 15px;

	color: rgba(255, 255, 255, .65);
}

.suggestList {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	width: 215px;
	max-height: 350px;
	padding: 5px 0 10px;

	background-color: var(--dx-toolbar-bg);
	border: 1px solid var(--dx-toolbar-bg);
}

.suggest {
	display: flex;
	align-items: center;
	height: 30px;
	margin: 5px 0;
	padding-left: 10px;
	padding-right: 15px;

	font-size: 16px;
}

.suggest_selected {
		background-color: var(--dx-dropdown-list-item-hover-bg);
	}

.suggest:last-child {
		margin-bottom: 8px;
	}

.selectedMark {
	color: inherit;
	background-color: transparent;
}

.description {
	color: rgba(255, 255, 255, .8);
}

.iconWrapper {
	position: absolute;
	top: 3px;
	right: -2px;
	width: 25px;
	height: 25px;

	color: var(--dx-input-default-icon);
}

.iconWrapper:hover {
		color: var(--dx-input-hover-icon);
	}

.iconWrapper:active {
		color: var(--dx-input-pressed-icon)
	}
