.container {
	height: 52px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 18px 0 4px;
}

.title {
	font-size: 16px;
	color: var(--dx-chart-instrument-label-text);
	text-transform: capitalize;
}

.controls {
	display: flex;
	align-items: center;
}

.menuTypeIcon {
	width: 24px;
	height: 24px;
	margin-right: 5px;
	color: var(--dx-chart-dropdown-list-icon);
}
