@import "./palette.css";

@font-face {
	font-family: 'Open Sans';

	src: url('../resources/fonts/opensans/opensans-bold.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-bold.woff') format('woff');

	font-weight: bold;
	font-style: normal;

}

@font-face {
	font-family: 'Open Sans';

	src: url('../resources/fonts/opensans/opensans-bolditalic.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-bolditalic.woff') format('woff');

	font-weight: bold;
	font-style: italic;

}

@font-face {
	font-family: 'Open Sans';

	src: url('../resources/fonts/opensans/opensans-italic.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-italic.woff') format('woff');

	font-weight: normal;
	font-style: italic;

}

@font-face {
	font-family: 'Open Sans';

	src: url('../resources/fonts/opensans/opensans-light.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-light.woff') format('woff');

	font-weight: 300;
	font-style: normal;

}

@font-face {
	font-family: 'Open Sans';

	src: url('../resources/fonts/opensans/opensans-regular.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-regular.woff') format('woff');

	font-weight: normal;
	font-style: normal;

}

@font-face {
	font-family: 'Open Sans';
	src: url('../resources/fonts/opensans/opensans-semibold.woff2') format('woff2'),
	url('../resources/fonts/opensans/opensans-semibold.woff') format('woff');

	font-weight: 600;
	font-style: normal;

}

:root {
	--dx-font-base: 'Open Sans', sans-serif;
	--dx-aside-width: 93px;
	--dx-side-panel-spacing: 13px;
	--dx-side-panel-width: 417px;
	--dx-standart-border-radius: 10px;
	--dx-header-height: 74px;
	--dx-mobile-header-height: 40px;
}
