.container {
	display: inline-block;
	position: relative;
	height: 30px;
	box-sizing: border-box;

	font-family: inherit;
	line-height: normal;

	background-color: var(--dx-input-default-bg);
	border: 0;
	border-bottom: 1px solid var(--dx-input-default-border);
	border-radius: 0;
	color: var(--dx-input-default-text);

	outline: none;

}

	.container:hover {
		border-color: var(--dx-input-hover-border);
	}

	.container_isFocused, .container_isFocused:hover {
			border-color: var(--dx-input-focused-border);
		}

	.container_hasError,
		.container_hasError:active,
		.container_hasError:focus,
		.container_hasError.container_isFocused,
		.container_hasError:hover {
			color: var(--dx-input-error-text);
			border-color: var(--dx-input-error-border);
		}

	.container[disabled],
		.container[disabled]:active,
		.container[disabled]:focus,
		.container[disabled].container_isFocused,
		.container[disabled]:hover,
		.container_isDisabled,
		.container_isDisabled:active,
		.container_isDisabled:focus,
		.container_isDisabled.container_isFocused,
		.container_isDisabled:hover {
			background-color: var(--dx-input-disabled-bg);
			border-color: var(--dx-input-disabled-border);
			color: var(--dx-input-disabled-text);
		}

.input {
	width: 100%;
	height: 100%;
	padding: 0 8px;

	font-family: var(--dx-font-base);
	font-size: inherit;
	line-height: inherit;
	vertical-align: top;

	background-color: transparent;
	border: none;
	box-shadow: none;
	color: inherit;

	outline: none;
}

.input::placeholder {
		color: var(--dx-input-placeholder-text);
	}

.errorText {
	color: var(--dx-form-error-text);
	margin-top: 5px;
}
