.row {
	border-width: 0;
	border-style: solid;
	border-color: var(--dx-table-divider-line);
	color: var(--dx-table-text);
	font-size: 18px;
	justify-content: space-between;
    display: flex;
    flex-direction: row;
	align-items: center;
	border-bottom-width: 1px;
	outline: none;
}

	.row:hover {
		background-color: var(--dx-table-selected-bg);
	}

.rowSelected {
	background-color: var(--dx-table-selected-bg);
}

.rowColumn {
	padding: 0 20px;
	display: flex;
	flex-grow: 0;
	flex-shrink: 1;
	align-items: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.rowColumn_small {
	padding: 0 14px;
}

.rowColumn_left {
	justify-content: flex-start;
}

.rowColumn_right {
	justify-content: flex-end;
}

.rowColumn_center {
	justify-content: center;
}

.positions {
	font-size: 12px;
	font-weight: 600;
	color: var(--dx-link-text);
	margin-left: 10px;
}

.red {
	color: var(--dx-table-down-text);
}

.green {
	color: var(--dx-table-up-text);
}

.controls {
	display: flex;
	align-items: center;
	justify-content: center;
	padding-right: 20px;
}

.control {
	padding: 0 2px;
	display: block;
	width: 40px;
	height: 40px;
	box-sizing: content-box;
	cursor: default;
}

.control:first-child {
	padding-left: 0;
}

.control:last-child {
	padding-right: 0;
}

.icon {
	padding: 0;
	width: auto;
	min-height: 0;
	color: var(--dx-table-icon);
	background: none;
	border: none;
	cursor: default;
}

.icon:hover, .icon:active {
		border: none;
	}

.icon:hover {
		background: var(--dx-close-button-hover-bg);
		border-radius: 4px;
		color: var(--dx-table-hover-icon);
	}

.icon:active {
		color: var(--dx-table-pressed-icon);
	}

.icon_plus {
	display: block;
	width: 24px;
	height: 24px;
	color: var(--dx-table-accent-icon);
}

.icon_plus:hover {
		color: var(--dx-table-accent-hover-icon);
	}

.icon_plus:active {
		color: var(--dx-table-accent-pressed-icon);
	}

.addProtection {
	width: auto;
	min-height: 0;
	padding: 0;

	font-size: 16px;
	text-align: center;

	color: var(--dx-link-text);
	background: none;
	border: none;
	cursor: default;
}

.addProtection:hover, .addProtection:active {
		background: none;
		border: none;
	}

.addProtection:hover {
		color: var(--dx-link-hover-text);
	}

.addProtection_small {
	font-size: 14px;
}
