

.container {
	display: flex;
	position: relative;
	background: var(--dx-app-bg);
}

.list {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	flex-grow: 1;
	width: var(--dx-aside-width);
}


.item {
	margin-right: 10px;
	margin-bottom: 8px;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 91px;
	min-height: 91px;
}

.item:first-child {
	margin-bottom: 0;
	height: var(--dx-header-height);
	min-height: var(--dx-header-height);
}

.item_break {
	margin-top: auto;
}

.link {
	position: relative;
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	color: white;
	text-decoration: none;
	border-radius: 0 var(--dx-standart-border-radius) var(--dx-standart-border-radius) 0;
}

.link:hover {
		background: var(--dx-navigation-item-hover-bg);
	}

.link:active {
		background: var(--dx-navigation-item-pressed-bg);
	}
.link_active {
	position: relative;
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	color: white;
	text-decoration: none;
	background: var(--dx-navigation-item-selected-bg);
}
.link_active:hover,
	.link_active:active {
		background: var(--dx-navigation-item-selected-bg);
	}

.icon {
	width: 50px;
	height: 50px;
}

.label {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	z-index: 3;
	top: 3px;
	right: 10px;
	width: 23px;
	height: 23px;
	border-radius: 50%;
	font-size: 14px;
	line-height: 1;
	letter-spacing: 0;
	color: white;
	background: var(--dx-navigation-item-counter-bg);
}

.title {
	margin-bottom: 10px;
	display: block;
	font-size: 14px;
	font-weight: 400;
	text-align: center;
	opacity: 0.85;
}

.menuIcon {
	display: block;
	width: 60px;
	height: 60px;
	color: var(--dx-main-menu-icon);
}

.menuIcon:hover {
		color: var(--dx-main-menu-hover-icon);
	}

.menuIcon:active {
		color: var(--dx-main-menu-pressed-icon);
	}
