.container__anchor {
		outline: none;
		border: none;
		background-color: var(--dx-dropdown-default-bg);
		color: var(--dx-dropdown-default-text);
	}

		.container__anchor:hover {
			background-color: var(--dx-dropdown-hover-bg);
			color: var(--dx-dropdown-hover-text);
		}

		.container__anchor:hover:before {
				background: none;
			}

		.container__anchor:hover .container__anchor__caret {
				color: var(--dx-dropdown-hover-icon);
			}

		.container__anchor:active {
			background-color: var(--dx-dropdown-pressed-bg);
			color: var(--dx-dropdown-pressed-text);
		}

		.container__anchor:active .container__anchor__caret {
				color: var(--dx-dropdown-pressed-icon);
			}

		.container__anchor:disabled, .container__anchor:disabled:hover {
				color: var(--dx-dropdown-disabled-text);
				background-color: var(--dx-dropdown-disabled-bg);
			}

		.container__anchor:disabled .container__anchor__caret, .container__anchor:disabled:hover .container__anchor__caret {
					color: var(--dx-dropdown-disabled-icon);
				}

		.container__anchor__text {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}

		.container__anchor__content {
			text-align: left;
		}

		.container__anchor__content_hasCaret {
				position: relative;
				padding-right: 13px;
			}

		.container__anchor__content_hasCaret .container__anchor__caret {
					display: block;
					position: absolute;
					right: 0;
					width: 10px;
					height: 5px;
					transform: none;
					color: var(--dx-dropdown-default-icon);
					top: 6px;
					transition: transform .2s ease;
				}

		.container__anchor__content_hasCaret .container__anchor__caret_isReversed {
						transform: rotate(180deg);
					}
	.container_isOpened {
		background-color: var(--dx-dropdown-pressed-bg);
		color: var(--dx-dropdown-pressed-text);
	}
	.container_isOpened .container__anchor__caret {
			color: var(--dx-dropdown-pressed-icon);
		}
	.container__text {
		color: var(--dx-dropdown-list-default-text);
	}
	.container__menu {
		display: flex;
		flex-direction: column;
	}
	.container__menu_hasSelectedItem .container__item {
				padding-right: 13px;
				position: relative;
			}
	.container__menu_hasSelectedItem .container__item__activeIcon {
					display: block;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					width: 8px;
					height: 8px;
				}
	.container__item {
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.container__item__text {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	.container__popover__content {
		box-shadow: 0 2px 10px 0 var(--dx-dropdown-list-shadow);
		padding: 0;
	}
