.row {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	height: 60px;
	font-size: 18px;
	color: var(--dx-table-text);
	border-width: 0;
	border-style: solid;
	border-color: var(--dx-table-divider-line);
	border-top-width: 1px;
	outline: none;
}

	.row:hover {
		background-color: var(--dx-table-selected-bg);
	}

.rowColumn {
	display: flex;
	flex-grow: 0 !important;
	flex-shrink: 1;
	align-items: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	justify-content: center;
}

.rowColumn:first-child {
		justify-content: flex-start;
	}

.rowColumn:last-child {
		text-overflow: unset;
	}

.cell {
	padding: 6px 20px;
}

.cell_empty, .seconds {
	color: var(--dx-table-inactive-text);
}

.cell_numeric {
	margin-left: auto;
	text-align: right;
}

.cell_buy {
	color: var(--dx-table-up-text);
}

.cell_sell {
	color: var(--dx-table-down-text);
}

.cell_positive {
	color: var(--dx-metrics-positive-text);
}

.cell_negative {
	color: var(--dx-metrics-negative-text);
}

.infoCell {
	text-align: right;
	width: 100%;
}

.icon {
	display: inline-block;
	width: 40px;
	height: 40px;
	vertical-align: middle;
	color: var(--dx-table-icon);
}

.icon:hover {
		background: var(--dx-close-button-hover-bg);
		border-radius: 4px;
		color: var(--dx-table-hover-icon);
	}

.icon:active {
		color: var(--dx-table-pressed-icon);
	}
