.container__anchor {
		font-size: 16px;
		color: var(--dx-chart-dropdown-text);
		width: 70px;
		height: 70px;
		padding: 0;
		border: none;
		background: transparent;
		border-radius: 5px;
	}

		.container__anchor:hover {
			background-color: var(--dx-locale-menu-bg);
			color: var(--dx-chart-dropdown-text);
		}

		.container__anchor:hover .container__anchor__caret {
				color: var(--dx-chart-dropdown-icon);
			}

		.container__anchor:active {
			background-color: var(--dx-locale-menu-bg);
			color: var(--dx-chart-dropdown-text);
		}

		.container__anchor:active .container__anchor__caret {
				color: var(--dx-chart-dropdown-icon);
			}

		.container__anchor__content {
			display: flex;
			margin-left: 13px;
			padding: 0;
		}

		.container__anchor__content_hasCaret .container__anchor__caret {
					width: 7px;
					height: 4px;
					color: var(--dx-chart-dropdown-icon);
					margin-left: 6px;
					position: initial;
				}

		.container__anchor__text {
			color: var(--dx-chart-dropdown-icon);
		}
	.container_isOpened {
		background-color: var(--dx-locale-menu-bg);
		border-radius: 0 0 5px 5px;
		box-shadow: 0 0 20px 0 var(--dx-dropdown-list-shadow);
	}
	.container__popover {
		left: -90px;
		top: 0;
		width: 160px;
	}
	.container__popover:after {
			content: '';
			display: block;
			position: absolute;
			bottom: -17px;
			right: 0;
			height: 17px;
			width: 70px;
			background-color: var(--dx-locale-menu-bg);
			pointer-events: none;
		}
	.container__popover__content {
			overflow: auto;
			padding: 0;
			border: none;
			border-radius: 10px 10px 0 0;
		}
	.container__item {
		width: 100%;
	}
	.container__item__text {
		display: flex;
		align-items: center;
		text-transform: capitalize;
	}
