.container {
    width: 15px;
    height: 20px;
    min-height: 20px;
	padding: 0;
    margin-right: 20px;
    margin-bottom: 5px;
	background: var(--dx-input-default-bg);
	border: 0;
}

	.container:hover, .container:active {
		background: var(--dx-input-default-bg);
	}

	.container:last-child {
		margin-right: 0px;
	}
