.container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
	height: 100%;
	border-radius: var(--dx-standart-border-radius) 0 0 0;
	border-right: none;
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 75px;
	padding: 10px 14px 10px 20px;

	border-radius: 10px 0 0 0;
	background: var(--dx-toolbar-header-bg);
}

.content {
	flex: 1;
	overflow-y: auto;

	background-color: var(--dx-toolbar-bg);
}

.footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	padding: 30px 0;

	background-color: var(--dx-toolbar-footer-bg);
}

.body {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: 100%;
	padding: 0 20px;
	width: 100%;
}
