.container {
	display: flex;
	width: 100%;
	height: 100%;
	background: var(--dx-widget-bg);
	border-radius: 4px;
}

.content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-left: auto;
	margin-right: auto;
}

.textWrapper {
	margin-top: 20px;
	margin-bottom: 40px;
}

.title {
	font-size: 24px;
	line-height: 1.56;
	text-align: center;

	color: var(--dx-empty-screen-title-text);
}

.title_preview {
		width: 350px;
	}

.subtitle {
	font-size: 18px;
	line-height: 2;
	text-align: center;

	color: var(--dx-empty-screen-subtitle-text);
}

.iconContainer {
	width: 277px;
	height: 189px;
	color: var(--dx-empty-screen-icon);
}
