.container {
	display: flex;
	width: 100%;
	height: 100%;
}

.symbols {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	width: 240px;

	background: var(--dx-widget-bg);
	border-right: 1px solid var(--dx-table-section-line);
	color: var(--dx-table-text);
}

.watchlist {
	flex-grow: 1;
}

.left {
	display: flex;
	flex: 1;
	align-items: stretch;
	flex-direction: column;
	position: relative;
	height: 100%;
	margin-right: var(--dx-side-panel-spacing);
}

.aside {
	display: flex;
	position: relative;
	width: var(--dx-side-panel-width);
	height: 100%;
	overflow: hidden;

	border-radius: var(--dx-standart-border-radius) 0 0 0;
	background-color: var(--dx-widget-bg);
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.25);
}
