:root {
	--dx-menu-arrow-icon: rgba(255, 255, 255, .8);
	--dx-menu-list-bg: rgba(0, 0, 0, .15);
}

.current {
	position: relative;
}

.current_opened .icon {
		transform: rotate(180deg);
	}

.icon {
	width: 20px;
	height: 100%;
	color: var(--dx-menu-arrow-icon);
	position: absolute;
	right: 17px;
	top: 0;
	transition: transform .3s;
}

.list {
	background-color: var(--dx-menu-list-bg);
}
