.item {
	display: flex;
	align-items: baseline;
	height: 20px;

	line-height: 20px;
	font-size: 14px;

	color: var(--dx-form-text);
}

	.item_secondary {
		height: auto;
	}

	.item_secondary .label {
			margin-left: 190px;
			width: 60px;

			text-align: left;
		}

	.item:not(:last-child) {
		margin-bottom: 20px;
	}

.label {
	color: var(--dx-form-label-text);
	width: 190px;
}

.value {
	position: relative;
	flex-grow: 1;
	line-height: 20px;

	font-size: 16px;
}
