:root {
	--dx-overlay: rgba(1, 14, 25, 0.5);
	--dx-menu-shadow: rgba(0, 0, 0, 0.8);
	--dx-menu-header-height: 84px;
	--dx-menu-footer-height: 104px;
}


.container {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: var(--dx-aside-width);
	background-color: var(--dx-overlay);
	color: var(--dx-navigation-item-text);
	/* To keep above the chart tools */
	z-index: 5;
}

.menuContainer {
	position: relative;
	padding-bottom: 20px;
}

.panel {
	height: 100%;
	width: 358px;
	box-shadow: 0 2px 10px 0 var(--dx-menu-shadow);
	background-color: var(--dx-app-bg);
	font-size: 16px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.wrapper {
	display: flex;
	flex-direction: column;
	flex: 1;
	max-height: calc(100% - var(--dx-menu-footer-height));
}

.header {
	padding: 16px 16px 16px 13px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	height: var(--dx-menu-header-height);
	background: var(--dx-menu-header-bg);
}

.account {
	display: block;
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-family: var(--dx-font-base);
	font-size: 22px;
	font-weight: 400;
	line-height: 1.36;
	letter-spacing: normal;
	color: #ffffff;
}
.accountLabel {
	font-family: var(--dx-font-base);
	font-size: 16px;
	font-weight: 300;
	line-height: 1.38;
	color: var(--dx-menu-badge-demo);
}
.accountLabel_real {
	color: var(--dx-menu-badge-real);
}
.accountLabel_preview {
	color: var(--dx-button-cta-default-bg);
}

.footer {
	margin: 0 23px;
	display: flex;
	border-top: 1px solid var(--dx-navigation-divider);
	font-size: 16px;
	height: var(--dx-menu-footer-height);
}

.logoutIcon {
	margin-right: -4px;
	display: block;
	height: 60px;
	width: 60px;
	color: var(--dx-main-menu-icon);
}

.logout {
	width: 100%;
}

.email {
	font-size: 16px;
	font-weight: 300;
	color: var(--dx-subnavigation-item-secondary-text);
	margin-left: 56px;
	margin-top: -10px;
}

.logoutLinkEmailWrapper {
	margin-left: -7px;
	padding: 12px 0 18px 0;
}

.logoutIconWrapper {
	display: flex;
	font-size: 14px;
	align-items: center;
}

.localeSelectorWrapper {
	margin-top: 9px;
	margin-bottom: 9px;
	margin-left: auto;
	padding-left: 10px;
	border-left: 1px solid var(--dx-navigation-divider);
	display: flex;
	align-items: center;
}
