.container {
	user-select: none;
	position: relative;
}

.item,
.itemGroup__header {
	cursor: default;
	transition: background-color .2s ease, color .2s ease;
}

.item:first-child, .itemGroup__header:first-child {
		border-radius: 8px 8px 0 0;
	}

.item:hover, .itemGroup__header:hover {
		background-color: var(--dx-dropdown-list-item-hover-bg);
	}

.item_active {
	background-color: var(--dx-dropdown-list-item-hover-bg);
}

.item__content, .itemGroup__header__content {
		padding-left: 10px;
		padding-right: 10px;
		height: 40px;
		display: flex;
		align-items: center;
	}
