.content {
	padding: 0 17px;
	height: 100%;
}

.orderChainId {
	font-size: 20px;
	font-weight: 600;
	color: var(--dx-sidepanel-header-text);
}

.symbol {
	font-size: 16px;
	color: var(--dx-sidepanel-subheader-text);
	position: relative;
	letter-spacing: 1px;
	padding-left: 1px;
}

.positiveSide {
	color: var(--dx-form-up-text);
}

.negativeSide {
	color: var(--dx-form-down-text);
}

.seconds {
	color: var(--dx-table-inactive-text);
}
