.window {
	display: inline-block;
	width: 100%;
	background: var(--dx-scrollbar-default-bg);
	height: 4px;
	border-radius: 2px;
}

.container {
	position: relative;
}

.chartMap {
	position: relative;
}

.map {
	height: 34px;
}

.canvasWrapper {
	position: relative;
	margin: 0 18px;
	height: 100%;
	background: var(--dx-widget-bg);
}
.canvas {
	display: none;
}
.labels {
	display: none;
	position: absolute;
	top:0;
	width: 100%;
	height: 100%;
}

.labels > div {
	position: absolute;
	height: 100%;
	border-left:  1px solid;
}

.controls {
	position: absolute;
	top: 15px;
	display: table;
	width: 100%;
	height: 100%;
}

.button {
	display: none;
	position: absolute;
	top: 0;
	width: 18px;
	height: 38px;
	border: 1px solid;
	font-weight: bold;
}

.button_toLeft {
	left: 0;
	border-radius: 5px 0 0 5px;
}

.button_toRight {
	right: 0;
	border-radius: 0 5px 5px 0;
}

.area {
	display: table-cell;
}

.area_hidden {
	width: 10px;
	height: 100%;
	vertical-align: middle;
}

.area:first-child {
	text-align: right;
}

.knob {
	display: none;
	width: 5px;
	height:25px;
	border: 1px solid;
	background: white;
	cursor: e-resize;
}

.knob_toLeft {
	right: 0;
	margin-right: -4px;
	border-radius: 5px 0 0 5px;
}

.knob_toRight {
	left: 0;
	margin-left: -4px;
	border-radius: 0 5px 5px 0;
}
