.container {
	padding: 0;
	width: auto;
	font-size: 16px;
	min-height: 0;
	color: var(--dx-link-text);
	background: none;
	border: none;
	cursor: default;
	margin: 0;
}

.container:hover, .container:active {
	background: none;
	border: none;
}

.container:hover {
	color: var(--dx-link-hover-text);
}
