.container {
	position: absolute;
	left: 0;
	width: 100%;
	border-bottom: 1px solid var(--dx-chart-highlighted-price-line);
}


.label {
	font-size: 12px;
	padding-left: 6px;
	position: absolute;
	bottom: 2px;
	color: var(--dx-chart-highlighted-price-line-label-text);
}

.price {
	position: absolute;
	padding: 0 4px;
	right: -4px;
	top: 50%;
	bottom: 0;
	border-radius: 4px;
	background-color:  var(--dx-chart-highlighted-price-bg);
	color: var( --dx-chart-highlighted-price-text);
	font-size: 14px;
	font-weight: 400;
	line-height: 26px;
	height: 26px;
	text-align: center;
	pointer-events: none;
	transform: translate(100%, -50%);
	z-index: 10;
}
