.container {
	display: inline-block;
	padding-bottom: 16px;

	white-space: nowrap;

	background-color: var(--dx-calendar-bg);
	border-radius: var(--dx-standart-border-radius);

	user-select: none;
	cursor: default;
}

.calendar {
	display: inline-block;
	vertical-align: top;
}

.calendar__header {
	font-weight: bold;
	font-size: 11px;
	color: var(--dx-calendar-header);
}

.calendar__header .calendar__day {
	height: 33px;
	width: 40px;

	line-height: 4px;
	font-size: 10px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 4;
	letter-spacing: normal;
	text-align: center;

	color: var(--dx-calendar-week-label-text);
}

.calendar__day {
	width: 30px;

	text-align: center;
	font-family: var(--dx-font-base);
}

.calendar__row {
	display: flex;
	padding-left: 10px;
	padding-right: 10px;
}

.calendar__body {
	font-size: 12px;
}

.calendar__body .calendar__day {
	padding: 0px;
	margin-right: 4px;
	margin-left: 6px;

	font-size: 12px;
	line-height: 1.88;

	background-color: transparent;
	border: 0px;
	color: var(--dx-calendar-day-text);

	transition: none;
}

.calendar__body .calendar__day:hover {
	background-color: var(--dx-calendar-day-hover);
}

.calendar__body .calendar__day:disabled,
.calendar__body .calendar__day_isHidden,
.calendar__body .calendar__day_isHidden[disabled],
.calendar__body .calendar__day_isHidden[disabled]:hover,
.calendar__body .calendar__day_isHidden:active,
.calendar__body .calendar__day_isHidden[disabled]:active {
	background-color: transparent;
	border-color: transparent;
	color: var(--dx-calendar-day-text-disabled);
}

.calendar__body .calendar__day_isCurrent,
.calendar__body .calendar__day_isCurrent[disabled],
.calendar__body .calendar__day_isCurrent:hover,
.calendar__body .calendar__day_isCurrent[disabled]:hover,
.calendar__body .calendar__day_isCurrent:active,
.calendar__body .calendar__day_isCurrent[disabled]:active {
	color: var(--dx-calendar-day-current-text);
}

.calendar__monthName {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 64px;
	margin: 0 12px;
	box-sizing: content-box;

	font-weight: bold;

	border-bottom: 1px solid var(--dx-calendar-divider-line);
}

.calendar__yearsText {
	color: var(--dx-calendar-year-text);
	margin-left: 10px;
	font-size: 16px;
	margin-right: 10px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 2.5;
	letter-spacing: normal;
	width: 125px;
	text-align: center;
}

.button_icon__wrapper {
	align-self: center;
}
