.container {
	display: flex;
	flex-grow: 1;
	height: 100%;
	align-items: center;
	justify-content: center;
}
.loader {
	position: relative;
	width: 50px;
	height: 50px;
}
.loader div {
	position: absolute;
	border: 3px solid #8098AA;
	opacity: 1;
	border-radius: 50%;
	animation: loader 0.8s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}
.loader div:nth-child(2) {
	animation-delay: -0.4s;
}
@keyframes loader {
	0% {
		top: 22px;
		left: 22px;
		width: 6px;
		height: 6px;
		opacity: 1;
	}
	100% {
		top: 0;
		left: 0;
		width: 50px;
		height: 50px;
		opacity: 0;
	}
}
