.container {
	display: flex;
	position: relative;
	user-select: none;
	align-items: center;
}

.container .input {
	position: absolute;
	visibility: hidden;
}

.container_isDisabled {
	opacity: 0.5;
}

.checkboxContainer {
	display: flex;
	align-items: center;
	justify-content: center;
}

.icon {
	display: block;
	margin: 0 auto;
	visibility: hidden;
	width: 18px;
	height: 18px;
	color: var(--dx-checkbox-radio-selected-default-icon);
}

.icon_isChecked {
	visibility: visible;
}

.view {
	display: flex;
	position: relative;
	width: 18px;
	height: 18px;
	border-radius: 4px;
	border: solid 2px var(--dx-checkbox-radio-unselected-default-border);
	background-color: var(--dx-checkbox-radio-unselected-default-bg);
	color: var(--dx-checkbox-radio-label-default-text);
	align-items: center;
	transition: border-color .3s, background-color .3s;
}

.view:hover {
	border-color: var(--dx-checkbox-radio-unselected-hover-border);
	background-color: var(--dx-checkbox-radio-unselected-hover-bg);
}

.view:active {
	border-color: var(--dx-checkbox-radio-unselected-pressed-border);
	background-color: var(--dx-checkbox-radio-unselected-pressed-bg);
}

.view_isChecked {
	border: solid 2px var(--dx-checkbox-radio-selected-default-border);
	background-color: var(--dx-checkbox-radio-selected-default-bg);
}

.view_isChecked:hover {
		border-color: var(--dx-checkbox-radio-selected-default-border);
		background-color: var(--dx-checkbox-radio-selected-hover-bg);
	}

.view_isChecked:hover .icon {
			color: var(--dx-checkbox-radio-selected-hover-icon);
		}

.view_isChecked:active {
		border-color: var(--dx-checkbox-radio-selected-pressed-border);
		background-color: var(--dx-checkbox-radio-selected-pressed-border);
	}

.view_isChecked:active .icon {
			color: var(--dx-checkbox-radio-selected-pressed-icon);
		}

.view_isDisabled, .view_isDisabled:hover, .view_isDisabled:active {
		border-color: var(--dx-checkbox-radio-unselected-disabled-border);
		background-color: var(--dx-checkbox-radio-unselected-disabled-bg);
	}

.view_isDisabled .icon, .view_isDisabled:hover .icon, .view_isDisabled:active .icon {
			color: var(--dx-checkbox-radio-selected-disabled-icon);
		}

.view_isCheckedDisabled, .view_isCheckedDisabled:hover, .view_isCheckedDisabled:active {
		border-color: var(--dx-checkbox-radio-selected-disabled-border);
		background-color: var(--dx-checkbox-radio-selected-disabled-bg);
	}

.view_isCheckedDisabled .icon, .view_isCheckedDisabled:hover .icon, .view_isCheckedDisabled:active .icon {
			color: var(--dx-checkbox-radio-selected-disabled-icon);
		}

.text {
	margin-left: 10px;
	font-size: 16px;
	line-height: 20px;
	color: var(--dx-checkbox-radio-label-default-text);
}
