.container {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 40px;
	width: auto;
	padding: 0 40px;

	font-family: var(--dx-font-base);
	font-size: 16px;
	font-weight: 600;
	white-space: nowrap;

	color: var(--dx-button-cta-default-text);
	background: var(--dx-button-cta-default-bg);
	border-color: var(--dx-button-cta-default-border);
	border-radius: 20px;
	border-width: 2px;
}

	.container:hover {
		background-color: var(--dx-button-cta-hover-bg);
		border-color: var(--dx-button-cta-default-border);
	}

	.container:active {
		background-color: var(--dx-button-cta-pressed-bg);
		border-color: var(--dx-button-cta-pressed-bg);
	}

	.container:disabled {
		background-color: var(--dx-button-cta-disabled-bg);
		border-color: var(--dx-button-cta-disabled-border);
		color: var(--dx-button-cta-disabled-text);
	}
