:root {
	--dx-menu-navigation-item-disabled: rgba(255, 255, 255, .4);
	--dx-menu-navigation-item-icon-disabled: rgb(31, 73, 111);
}

.container {
	margin: 0;
	padding: 15px 7px 14px;
	display: flex;
	font-size: 14px;
	align-items: center;
	color: var(--dx-navigation-item-text);
	cursor: details;
}

.container:hover {
		 background-color: var(--dx-navigation-item-hover-bg);
	}

.container:hover .icon {
			color: var(--dx-main-menu-icon-hover);
		}

.container_disabled {
	color: var(--dx-menu-navigation-item-disabled);
}

.container_disabled:hover {
		background-color: transparent;
	}

.container_disabled:hover .icon {
			color: var(--dx-menu-navigation-item-icon-disabled);
		}

.container_disabled .icon {
		color: var(--dx-menu-navigation-item-icon-disabled);
	}

.icon {
	width: 60px;
	height: 60px;
	color: var(--dx-main-menu-icon);
	display: inline-block;
	margin-right: -4px;
}
