.track {
	position: relative;
	width: 100%;
	height: 100%;
}

.bar {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	min-height: 10px;
	border-radius: 1px;
}

.bar:before {
		content: '';
		top: 3px;
		bottom: 3px;
		width: 3px;
		position: absolute;
		border-radius: 1px;
		background: var(--dx-scrollbar-default-bg);
	}

.bar:hover:before, .bar:active:before {
		background: var(--dx-scrollbar-active-bg);
	}

.container {
	position: absolute;
	box-sizing: border-box;
	visibility: hidden;
	z-index: -1;
	top: 0;
	right: 4px;
	bottom: 0;
	padding: 0;
	width: 3px;
}

.containerIsVisible {
	visibility: inherit;
	z-index: auto;
}
