.container {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex: 1;
}

.section {
	display: flex;
	flex-direction: column;
	min-width: 164px;
	align-items: center;
}

.section__header {
	font-size: 14px;
	color: var(--dx-metrics-label-text);
	line-height: 0.71;
	margin-bottom: 10px;
}

.section__value {
	font-size: 16px;
	color: var(--dx-metrics-default-text);
	line-height: 0.88;
}

.separator {
	height: 15px;
	border-right: 1px solid var(--dx-metrics-divider-icon);
}
