.content {
	height: 100%;
}

.symbol {
	font-size: 20px;
	font-weight: 600;
	color: var(--dx-sidepanel-header-text);
}

.description {
	font-size: 16px;
	color: var(--dx-sidepanel-subheader-text);
	position: relative;
	padding-top: 1px;
}

.marketSentiment {
	display: flex;
	justify-content: space-between;
	position: relative;
	top: -10px;
	width: 142px;

	font-size: 14px;
}

.marketSentimentBar {
	position: absolute;
	top: 22px;
	left: 0;
	width: 100%;
	height: 4px;
	border-radius: 2px;
	background-color: var(--dx-table-up-icon);
}

.marketSentimentBarLeft {
	height: 4px;
	border-radius: 2px 0 0 2px;
	background-color: var(--dx-table-down-icon);
}

.prefixChange {
	position: absolute;
	top: 0;
	left: -11px;
	line-height: 16px;
}

.tradingHours {
	display: block;
	margin-top: 20px;
	line-height: 1.2;
}

.tradingHours:first-child {
		margin-top: 0;
	}
