/*head*/
.table > .row:first-of-type {
	font-weight: 300;
	font-size: 15px;
	color: var(--dx-table-header-text);
	overflow-y: hidden;
	background-color: var(--dx-table-header-bg);
	text-transform: none;
}

.body {
	background-color: var(--dx-widget-bg);
	outline: none;
}

.row {
	border-width: 0;
	border-style: solid;
	border-color: var(--dx-table-divider-line);
	color: var(--dx-table-text);
	font-size: 18px;
	justify-content: space-between;
}

.body .row {
	border-top-width: 1px;
	outline: none;
}

.body .row:hover {
		background-color: var(--dx-table-selected-bg);
	}

.body .row:first-of-type {
	border-top-width: 0;
}

.body .row:last-of-type {
	border-bottom-width: 1px;
}

.head {
	/* common styles */
	height: 43px;
	overflow: hidden;
	padding-right: 0;
	/* from ReactVirtualized__Table__headerRow */
	display: flex;
    flex-direction: row;
	align-items: center;
	/* row-specific */
	margin-right: 0;
	font-weight: 300;
	font-size: 15px;
	color: var(--dx-table-header-text);
	background-color: var(--dx-table-header-bg);
	text-transform: none;
}

.headCell {
	display: flex;
    align-items: center;
	margin-right: 0;
	flex-grow: 0;
	flex-shrink: 1;
}
