.row {
	border-width: 0;
	border-style: solid;
	border-color: var(--dx-table-divider-line);
	color: var(--dx-table-text);
	font-size: 18px;
	justify-content: space-between;
    display: flex;
    flex-direction: row;
	align-items: center;
	border-bottom-width: 1px;
	outline: none;
}

	.row:hover {
		background-color: var(--dx-table-selected-bg);
	}

.rowSelected {
	background-color: var(--dx-table-selected-bg);
}

.rowColumn {
	display: flex;
	flex-grow: 0;
	flex-shrink: 1;
	align-items: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.symbolCell {
	padding: 11px 20px;
}

.positions {
	font-size: 16px;
	font-weight: 400;
	color: var(--dx-link-text);
	margin-left: 30px;
	cursor: default;
}

.positions:hover {
		color: var(--dx-link-hover-text);
	}

.changeCell {
	padding: 6px 11px 6px 0;
	display: flex;
	align-items: center;
	width: 100%;
}

.changeCell_empty {
		padding: 6px 11px;
		text-align: center;
		width: 100%;
		color: var(--dx-table-inactive-text);
	}

.icon {
	display: inline-block;
	width: 40px;
	height: 40px;
	vertical-align: middle;
	color: var(--dx-table-icon);
}

.icon:hover {
		background: var(--dx-close-button-hover-bg);
		border-radius: 4px;
		color: var(--dx-table-hover-icon);
	}

.icon:active {
		color: var(--dx-table-pressed-icon);
	}

.icon_accent {
	color: var(--dx-table-accent-icon);
}

.icon_accent:hover {
		background: var(--dx-close-button-hover-bg);
		border-radius: 4px;
		color: var(--dx-table-accent-hover-icon);
	}

.icon_accent:active {
		color: var(--dx-table-accent-pressed-icon);
	}

.infoCell {
	margin-left: auto;
}

.favCell {
	margin-left: 5px;
	margin-right: 40px;
}
