@import '../../../../config/styles.config.css';

:global * {
		box-sizing: border-box;
	}

:global a {
		color: var(--dx-link-text-default-color);
		text-decoration: none;
	}

:global html {
		font-family: 'Open Sans', Arial;
		font-size: 14px;
		touch-action: manipulation;
		-webkit-tap-highlight-color: transparent;
		min-height: 100%;
		height: 100%;
	}

:global body {
	  	line-height: normal;
		height: 100%;
		min-width: 1280px;
		overflow-x: auto;
		background: var(--dx-app-bg);
	}

:global #root {
		-webkit-overflow-scrolling: touch;
		height: 100%;
		min-height: 576px;
		display: flex;
		flex-direction: column;
		align-items: stretch;
	}
