.content {
	padding-right: 16px;
	padding-left: 18px;
	flex: 1;
}

.header {
	display: flex;
	width: 100%;
	align-items: center;
}

.widgetLabel {
	color: var(--dx-table-header-text);
	font-size: 20px;
	line-height: 27px;
}

.closeBtnWrapper {
	margin-left: auto;
}

.red {
	color: var(--dx-table-down-text);
}
.green {
	color: var(--dx-table-up-text);
}
.seconds {
	opacity: 0.5;
}
.icon {
	position: absolute;
	left: -25px;
	top: 5px;
}
