:global .preloader-window-container {
        background-color: #023256;
        position: absolute;
        display: flex;
        flex-grow: 1;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        align-items: center;
        justify-content: center;
    }
    :global .preloader-window-loader {
        position: relative;
        width: 80px;
        height: 80px;
    }
    :global .preloader-window-loader div {
        position: absolute;
        border: 3px solid #8098AA;
        opacity: 1;
        border-radius: 50%;
        animation: preloader-window-loader 0.8s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
    }
    :global .preloader-window-loader div:nth-child(2) {
        animation-delay: -0.4s;
    }
    @keyframes  :global(preloader-window-loader) {
        0% {
            top: 37px;
            left: 37px;
            width: 6px;
            height: 6px;
            opacity: 1;
        }
        100% {
            top: 0;
            left: 0;
            width: 80px;
            height: 80px;
            opacity: 0;
        }
    }
