.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 54px;
	width: 50%;
	background-color: var(--dx-side-toggle-default-bg);
	border: 2px solid var(--dx-side-toggle-default-border);
	color: var(--dx-side-toggle-primary-text);
}

	.container:hover {
		background-color: var(--dx-side-toggle-hover-bg);
	}

	.container_sell {
		background-color: var(--dx-sell-side-toggle-default-bg);
		border-color: var(--dx-sell-side-toggle-default-bg);
	}

	.container_sell:hover {
			background-color: var(--dx-sell-side-toggle-default-bg);
		}

	.container_buy {
		background-color: var(--dx-buy-side-toggle-default-bg);
		border-color: var(--dx-buy-side-toggle-default-bg);
	}

	.container_buy:hover {
			background-color: var(--dx-buy-side-toggle-default-bg);
		}

	.container_disabled {
		opacity: .5;
	}

	.container_disabled:hover {
			background-color: var(--dx-side-toggle-default-bg);
			border-color: var(--dx-side-toggle-default-border);
		}

	.container:first-child {
		border-radius: 27px 0 0 27px;
		border-right: 0;
	}

	.container:last-child {
		border-radius: 0 27px 27px 0;
		border-left: 0;
	}

.text {
	font-size: 13px;
	line-height: 18px;
	text-transform: uppercase;
	color: var(--dx-side-toggle-secondary-text);;
}
