.container {
	width: 30px;
	height: 30px;
	padding: 0;
	border: none;
	background: var(--dx-close-button-default-bg);
	border-radius: 4px;
}

	.container:hover {
		background: var(--dx-close-button-hover-bg);
	}

	.container:hover .icon {
			color: var(--dx-close-button-hover-icon);
		}

	.container:active {
		background: var(--dx-close-button-pressed-bg);
	}

	.container:active .icon {
			color: var(--dx-close-button-pressed-icon);
		}

.icon {
	display: block;
	height: 30px;
	width: 30px;
	color: var(--dx-close-button-default-icon);
}
