.container {
	flex-grow: 1;
	position: relative;
	height: 100%;
	padding: 8px 15px 8px 0;
}

	.container_hasShadow {
		box-shadow: inset 0 -50px 25px -25px var(--dx-widget-inner-shadow);
	}

.categories {
	display: flex;
	flex-direction: column;
}

.link {
	padding: 12px 20px;
	overflow: hidden;

	font-size: 16px;
	text-overflow: ellipsis;
	text-transform: capitalize;
	white-space: nowrap;

	cursor: default;
}

.link:hover {
		background: var(--dx-table-selected-bg);
	}

.link_active {
		font-weight: 600;
		background: var(--dx-table-selected-bg);
	}

.title {
	display: flex;
	align-items: center;
	padding: 16px 0 16px 20px;

	font-size: 15px;
	line-height: 0.83em;

	color: var(--dx-table-section-caption-text);
}

.title:after {
	display: block;
	flex-grow: 1;
	height: 1px;
	margin-left: 10px;
	background: var(--dx-table-section-line);
	content: '';
}
