.content {
	padding: 40px 0 0;
	position: relative;
	width: 461px;
	text-align: center;
	font-family: var(--dx-font-base);
}

.text {
	margin: 0 57px 30px;
	font-size: 17px;
	line-height: 26px;
	color: var(--dx-margin-popup-text);
}

.link {
	display: inline-block;
	text-decoration: underline;
}

.link:hover {
		text-decoration: none;
	}
.buttons {
	margin: 0 40px 40px;
	display: flex;
	justify-content: space-between;
}
