.container {
	width: 60px;
	height: 28px;
	font-size: 15px;
	font-weight: normal;
	border-radius: 30px;
	background: var(--dx-button-primary-default-bg);
	border: 1px solid var(--dx-button-primary-default-border);
}

	.container:hover {
		background-color: var(--dx-button-primary-hover-bg);
		border-color: var(--dx-button-primary-hover-border);
	}

	.container:active {
		background-color: var(--dx-button-primary-pressed-bg);
		border-color: var(--dx-button-primary-pressed-border);
	}
