.container {
	display: flex;
	width: 100%;
	height: 60px;
	padding: 15px 20px;

	background-color: var(--dx-toolbar-bg);
	border-radius: 4px;
}

.dropdownWrapper {
	height: 30px;
	margin-right: 60px;
}

.text {
	font-size: 16px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 0.75;
	letter-spacing: normal;
	color: var(--dx-input-default-text);
}

.triangleWrapper {
  margin-left: auto;
  align-self: center;
  width: 15px;
  height: 15px;
}

.datePickersWrapper {
	height: 100%;
	display: inline-flex;
}

.dateInputWrapper {
	margin-left: 15px;
	margin-right: 45px;
}

.labelWrapper {
	display: flex;
	height: 100%;
	align-items: center;
	align-self: center;
}

.label {
	font-size: 16px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 0.75;
	letter-spacing: normal;
	color: var(--dx-form-label-text);
	align-self: center;
}

.error {
	display: flex;
	align-items: center;
	position: absolute;
	padding: 0 10px;

	font-size: 12px;

	/* TODO: replace to var when color will be added to palette */
	background-color: #022e4e;
	border: 1px solid var(--dx-form-input-error-border);
	border-radius: 3px;
	color: var(--dx-form-input-error-text);

	/* keeping above table */
	z-index: 1;
}
