:root {
	height: 100%;
}
:root body {
	height: 100%;
}

.container {
	display: flex;
	justify-content: space-between;
	position: relative;
	height: 100%;
}

.content {
	display: flex;
	flex-flow: column;
	flex-grow: 1;
}

.main {
	display: flex;
	width: 100%;
	height: 100%;
	color: #ffffff;
	min-height: 0;
}

.left {
	display: flex;
	flex: 1;
	height: 100%;
	position: relative;
	align-items: stretch;
	flex-direction: column;
	margin-right: 8px;
}

.aside {
	border-radius: 4px;
	background-color: var(--dx-widget-bg);
	position: relative;
	width: 375px;
	overflow: hidden;
}

.header {
	flex-shrink: 0;
}
