.container__anchor {
		margin-left: 24px;
		font-size: 14px;
		color: var(--dx-chart-dropdown-text);
		width: 56px;
		min-height: 40px;
		padding: 0;
		border: none;
		border-radius: var(--dx-standart-border-radius);
		background: transparent;
	}

		.container__anchor:hover {
			background-color: var(--dx-chart-dropdown-hover-bg);
			color: var(--dx-chart-dropdown-text);
		}

		.container__anchor:hover .container__anchor__caret {
				color: var(--dx-chart-dropdown-icon);
			}

		.container__anchor:active {
			background-color: var(--dx-chart-dropdown-pressed-bg);
			color: var(--dx-chart-dropdown-text);
		}

		.container__anchor:active .container__anchor__caret {
				color: var(--dx-chart-dropdown-icon);
			}

		.container__anchor__content {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
		}

		.container__anchor__content_hasCaret .container__anchor__caret {
					width: 15px;
					height: 15px;
					color: var(--dx-chart-dropdown-icon);
					margin-left: 4px;
					position: initial;
				}
	.container_isOpened {
		background-color: var(--dx-chart-dropdown-pressed-bg);
	}
	.container__popover {
		top: 5px;
		width: 63px;
	}
	.container__popover__content {
			width: 80px;
			border: solid 2px var(--dx-chart-dropdown-border);
			border-radius: var(--dx-standart-border-radius);
			overflow: hidden;
			z-index: 1; /* browser bug fix */
		}
	.container__menu {
		height: 240px;
		font-size: 14px;
	}
