.container {
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	position: absolute;
	z-index: 5;
}

.axis {
	position: absolute;
	top: 0;
	left: 0;
	display: none;
}

.axis_x {
		width: 100%;
		height: 1px;
		background: white;
	}

.axis_y {
		width: 1px;
		height: 100%;
		background: white;
	}
.chartInfo {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 3;
	display: none;
}
.chartInfo_fixed {
		display: block;
	}
.chartInfo_mobile {
		top: -186px;
		left: -25px;
	}
.chartInfo_alignedRight > * {
			transform: translateX(-100%);
		}
.axisLabel {
	position: absolute;
	top: 0;
	left: 0;
	display: none;
}
.axisLabel_visible {
		display: block;
	}
.axisLabel_y .axisLabelValue {
			transform: translateY(-50%);
		}
.axisLabel_x .axisLabelValue {
			transform: translateX(-50%);
		}
.axisLabel .axis {
		display: block;
	}
.visible .axis_y {
			display: block;
		}
.visible .axis_visible {
			display: block;
		}
.visible .chartInfo {
		display: block;
	}

.disabled .axis {
		display: none;
	}

.disabled .axisLabel {
		display: none;
	}

.disabled .chartInfo {
		display: none;
	}

.axis {
	background-color: var(--dx-chart-cursor-line);;
}
.axisLabelValue {
	color: var(--dx-chart-cursor-line);;
}

.chart--graph {
		position: relative;
	}

.chart--axisWrapper {
		display: table-cell;
	}

.chart--axisWrapper-x,
		.chart--axisWrapper-y {
			position: relative;
		}

.chart--axis-x {
			border: none;
		}
.crossTool {
	display: none;
}
