.wrapper {
	flex-grow: 1;
	position: relative;
	overflow: hidden;
}

.container {
	overflow: visible;
	display: flex;
	flex-direction: column;
	width: 100%;
	background: var(--dx-widget-bg);
}

.container canvas {
	width: 100%;
	height: 0;
	outline: none;
}

.axisWrapper-x,
.graph {
	display: table-row;
}

.graph_resizable {
	display: flex;
	overflow: hidden;
	height: 100%;
}

.axisWrapper_x {
	position: absolute;
	bottom: 22px;
	left: 0;
	-webkit-touch-callout: none;
	user-select: none;
}
.axisWrapper_y {
	margin-right: 2px;
	display: table-cell;
	min-width: 45px;
	vertical-align: top;
}

.axis_y > :global.last {
	display: none;
}

.axis {
	border: none;
}

.axis_y {
	position: relative;
	width: 100%;
	height: 100%;
	font-size: 15px;
	font-family: sans-serif;
}

.axis_x {
	position: relative;
	display: table-cell;
	padding: 3px 0 0;
	border: none;
	font-family: sans-serif;
}

.axis_x > div,
.axis_y > div {
	position: absolute;
	font-size: 14px;
}

.axis.axis_y:global.chart--axisExpander > div {
	position: relative;
	visibility: hidden;
	line-height: 0;
	margin-top: 0;
	margin-bottom: 0;
	padding-top: 0;
	padding-bottom: 0;
}

.axis_y > div {
	margin: -7px 5px 0;
	padding: 1px 3px;
}

.canvasArea {
	width: 100%;
}

.canvasWrapper {
	position: relative;
	width: 100%;
}
.canvasWrapper canvas {
	display: block;
}
.studyOverlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.studyMarker {
	display: inline-block;
	margin: 3px 3px 1px 7px;
	width: 7px;
	height: 7px;
}

.extraCanvas {
	position: absolute;
	top: 0;
	background: transparent;
}
